body {
  color: #2f2f37;
  background-color: #fff;
  font-family: monospace;
  line-height: 1.5;
}

.poke-container {
  padding-left: 15px;
  padding-right: 15px;
}

.poke_title {
  justify-content: center;
  display: flex;
}

.poke-form_box {
  color: #fff;
  text-align: center;
  background-color: #4b4747;
  flex-direction: column;
  padding-top: 30px;
  padding-bottom: 15px;
  display: flex;
}

.poke_form {
  color: #fff;
  border-radius: 2px;
  padding: 2px 6px;
  font-size: 16px;
  font-weight: 400;
}

.poke-input {
  border: none;
  border-radius: 10px;
  width: 600px;
  height: 30px;
  padding-left: 10px;
}

.card-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.card {
  color: #4b4747;
  max-width: 180px;
  margin-top: 30px;
}

.card-img-top {
  background-color: #d5d5d9;
  border-radius: 15px;
  width: 180px;
  height: 150px;
}

.card-img-top-card {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.card-body {
  border-width: 2px;
  border-color: #4b4747;
  border-radius: 15%;
}

.list-group {
  padding-left: 0;
  list-style: none;
  display: flex;
}

.more_btn {
  color: #fff;
  cursor: pointer;
  background-color: #4b4747;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.more_btn:hover {
  background-color: #715353;
}

.poke-filt_sort {
  justify-content: flex-end;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.poke_sort {
  color: #4b4747;
  background-color: #fff;
  border: 1px solid #4b4747;
  border-radius: 5px;
  padding: 5px 10px;
}
